import React from 'react'
import Animate from "grommet/components/Animate"
import Box from "grommet/components/Box"

export default class FourOFour extends React.Component{

  constructor() {
    super();
    this.state = {
      innerHtml: ""
    };
    this.generateInnerHtml = this.generateInnerHtml.bind(this);
    this.target_date = new Date('05/13/2061 01:00:00 PM').getTime();
  }

  componentDidMount() {
    const innerHtml = this.generateInnerHtml();
    this.intervalId = setInterval(this.generateInnerHtml, 1000);
  }

  componentWillUnmount(){
    clearInterval(this.intervalId);
  }

  generateInnerHtml(){
    let innerHtml = this.getCountdown(this);
    this.setState({
      innerHtml: innerHtml
    });
  }

  render(){
      let colors = this.props.setColorOfContent();
      let style = {
        backgroundColor: colors.video + "1a"
      }
      return(
        <Animate enter={{ animation: "slide-right", duration: 300, delay: 0 }} keep={true}>
          <Box className="content-box four-o-four" style={style}>
            <h1 className="content-title">404</h1>
            <p>Page not found </p>
              <p>Countdown - 13. 05. 2061 in 13:00:00 hours</p>
              <p className="quote" >As in the Olympic Games it is not the most beautiful and the strongest that are crowned but those who compete, so those who act rightly win the noble and good things in life.</p>
              <div className="countdown">
                <div className='tiles'  dangerouslySetInnerHTML={{ __html: this.state.innerHtml }}></div>
              </div>
            </Box>
        </Animate>
    )
  }

  getCountdown(){

    // find the amount of "seconds" between now and target
    let current_date = new Date().getTime();
    let seconds_left = (this.target_date - current_date) / 1000;

    let days = this.pad( parseInt(seconds_left / 86400) );
    seconds_left = seconds_left % 86400;
      
    let hours = this.pad( parseInt(seconds_left / 3600) );
    seconds_left = seconds_left % 3600;
      
    let minutes = this.pad( parseInt(seconds_left / 60) );
    let seconds = this.pad( parseInt( seconds_left % 60 ) );

    // format countdown string + set tag value
    return "<span>" + days + "</span><span>" + hours + "</span><span>" + minutes + "</span><span>" + seconds + "</span>"; 
  }

  pad(n) {
	  return (n < 10 ? '0' : '') + n;
  }
}


